import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue" //导航栏
import vanField from "@/wxcomponents/vant/dist/field" //输入框
export default {
	components: {
		uniNavBar,
		vanField
	},
	data() {
		return {
			// 桌号
			tableNum: '',
			// 手机号
			phone: '',
			// 用户名
			nickName: '',
			// 头像
			avator: '',
			// 系统时间
			time: ''
		}
	},
	mounted() {
		this.getTime()
		this.Init()
	},
	methods: {
		// 初始化我的页面
		Init() {
			var arr = uni.getStorageSync('token')
			var tableNum = uni.getStorageSync('tableNum')
			var phone = uni.getStorageSync('phone')
			this.avator = arr.avatarUrl
			this.nickName = arr.nickName
			this.tableNum = tableNum
			this.phone=phone
		},
		// 获取系统时间
		getTime() {
			var date = new Date(),
				year = date.getFullYear(),
				month = date.getMonth() + 1,
				day = date.getDate(),
				hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
				minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
				second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			month >= 1 && month <= 9 ? (month = "0" + month) : "";
			day >= 0 && day <= 9 ? (day = "0" + day) : "";
			var timer = year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
			this.time = timer
		},
		// 校验手机号
		checkMobile(mobile) {
			return RegExp(/^1[34578]\d{9}$/).test(mobile);
		},
		submitUserInfo() {
			if (!this.checkMobile(this.phone)) {
				uni.showToast({
					title: '手机号格式错误',
					icon: 'none'
				});
				return;
			}
			uni.setStorageSync('phone',this.phone)
			uni.navigateBack()
		},
		goback() {
			uni.navigateBack()
		}
	}
}
